.main-btn{
  background: $mainColor;
  color: #fff;
  font-size: 28px;
  width: 250px;
  border: 1px solid  $mainColor;
  transition: all  ease-in-out .4s;
  border-radius: 10px;
  &:hover{
    color:  $mainColor;
    background: #fff;
  }
}
.second-btn{
  background: $secondColor;
  color: #fff;
  font-size: 28px;
  width: 250px;
  border: 1px solid  $secondColor;
  transition: all  ease-in-out .4s;
  border-radius: 10px;
  &:hover{
    color:  $secondColor;
    background: #fff;
  }
}


